<template>
  <!-- Menu -->
  <div id="fullmenu">
    <a href="#" @click="log(mobile)">Clique aqui</a>
    <div v-if="mobile">
      <button type="button" class="btn btn-link" @click="toggleIsMenu()">
        <i class="fa fa-bars fa-3x iconmenu" id="menuicon"></i>
      </button>

      <button
        v-if="!getIsContactView"
        type="button"
        class="btn btn-link"
        @click="toggleIsContact()"
      >
        <i class="fa fa-phone fa-3x iconmenu" id="phoneicon"></i>
      </button>

      <transition name="bounce" mode="out-in">
        <!-- Menu navigatiebar -->
        <app-navbar v-if="getIsMenu"></app-navbar>
        <!-- Contactgegevens -->
        <app-contactmenu v-if="getIsContact"></app-contactmenu>
      </transition>
    </div>
    <div v-if="!mobile">
      <div id="navDesktop">
        <router-link to="/">Home</router-link>
        <router-link to="/about">Glow up</router-link>
        <router-link to="/makeup">Behandelingen</router-link>
        <router-link to="/arrangenworkshops">Arrangementen &amp; Workshops</router-link>
        <router-link to="/fotos">Fotoalbum</router-link>
        <router-link to="/contact">Contact</router-link>
      </div>
      <!-- Contactgegevens -->
      <div class="col-md-6" style="margin: 0 auto">
        <app-contactmenu v-if="!getIsContactView"></app-contactmenu>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
import NavbarVue from "./Navbar.vue";
import ContactmenuVue from "./Contactmenu.vue";
export default {
  data() {
    return {};
  },
  components: {
    appNavbar: NavbarVue,
    appContactmenu: ContactmenuVue
  },
  methods: {
    log: function(e) {
      console.log(e);
    },
    handleResize: state => {
      if (window.innerWidth < 992) {
        state.mobile = true;
      }
      if (window.innerWidth > 992) {
        state.mobile = false;
      }
    },
    ...mapActions({
      toggleIsMenu: "toggleIsMenu",
      toggleIsContact: "toggleIsContact"
    })
  },
  computed: {
    ...mapGetters({
      getIsMenu: "getIsMenu",
      getIsContact: "getIsContact",
      getIsContactView: "getIsContactView",
      mobile: "getIsMobile"
    })
  }
};
</script>

<style>
/* Full menu */
#fullmenu {
  margin: 1rem;
}

.iconmenu {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  text-align: center;
  line-height: 80px;
  font-size: 36px;
}

#phoneicon {
  background: #ff8d5b;
  color: beige;
}

#menuicon {
  background: beige;
  color: #ff8d5b;
}

/* Desktop menu */
#navDesktop a {
  font-weight: bold;
  color: #eee;
  text-decoration: none;
  display: inline-block;
  min-height: 2rem;
  line-height: 2rem;
  margin: 0.8rem;
}
#navDesktop a.router-link-exact-active {
  color: #ff8d5b;
}

#navDesktop a:hover {
  color: #ff8d5b;
}

.hrtje {
  border: 1px solid white;
}

/*  slide fade transition 
    for menu contact buttons    */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

/* bounce menu contact div */
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>
